// ** Reactstrap Imports
import { Card, CardBody, CardTitle, FormFeedback, Form, Label, Input, Button, Row, Col } from 'reactstrap'

// ** React Imports
import { Link, useLocation, useNavigate } from 'react-router-dom'

// ** Custom Hooks
import classnames from 'classnames'
import * as yup from 'yup'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'


import { verfiyCode, generateCode, listOrg } from './register-multi-steps/store/index'
// ** Actions
import { handleLogin } from '@store/authentication'

import { useDispatch, useSelector } from 'react-redux'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Styles
import '@styles/react/pages/page-authentication.scss'
import { Fragment, useEffect, useRef, useState } from 'react'
import axios from '@src/configs/axios/axiosConfig'
import toast from 'react-hot-toast'

const OTPInput = ({ length = 6, counter, otpfn }) => {
  const [otp, setOtp] = useState(Array(length).fill(''))
  const inputs = useRef([])
  const [seconds, setSeconds] = useState(59)
  const [focusIndex, setFocusIndex] = useState(-1)

  useEffect(() => {
    const interval = setInterval(() => {
      if (seconds > 0) {
        setSeconds(seconds - 1)
        counter(seconds - 1)
      }

      if (seconds === 0) {
        clearInterval(interval)
      }
    }, 1000)

    return () => {
      clearInterval(interval)
    }
  })

  const handleChange = (e, index) => {
    const { value } = e.target

    // Only allow single digit input
    if (value.match(/^\d$/)) {
      const newOtp = [...otp]
      newOtp[index] = value
      setOtp(newOtp)
      otpfn(newOtp)
      // Move focus to the next input
      if (index < length - 1) {
        setFocusIndex(index + 1)
        inputs.current[index + 1].focus()
      }
    }

    // Move focus to previous input on backspace
    if (value === '') {
      const newOtp = [...otp]
      newOtp[index] = ''
      setOtp(newOtp)
      otpfn(newOtp)
    }
    if (value === '' && index > 0) {
      setFocusIndex(index - 1)
      inputs.current[index - 1].focus()
    }
  }

  const handleKeyDown = (e, index) => {
    if (e.key === 'Backspace' && otp[index] === '') {
      // Move focus to previous input on backspace if current input is empty
      if (index > 0) {
        setFocusIndex(index - 1)
        inputs.current[index - 1].focus()
      }
    }
  }

  useEffect(() => {
    inputs.current[focusIndex]?.focus()
  }, [focusIndex])

  return (
    <Fragment>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        {otp.map((_, index) => (
          <input
            key={index}
            type="text"
            maxLength="1"
            value={otp[index]}
            onChange={(e) => handleChange(e, index)}
            onKeyDown={(e) => handleKeyDown(e, index)}
            ref={(el) => (inputs.current[index] = el)}
            className='width-15-per mx-50 text-center form-control'
          />
        ))}
      </div>
    </Fragment>
  )
}

const RegisterBasic = () => {

  // ** Hooks 
  const [block, setBlock] = useState(false)
  const [type, setType] = useState(1)
  const [verfiyCodesent, setVerifyCodeSent] = useState(false)

  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const codeRegExp = /^[0-9\- ]{4,4}$/
  const dispatch = useDispatch()
  const store = useSelector(state => state.register)
  const VerifySchema = yup.object().shape({
    email: yup.string().required('Email required'),
    emailCode: yup.string().required('Please Enter Verify Code').matches(codeRegExp, { message: 'Please Enter Valid verify Code', excludeEmptyString: true }),
    acceptTermsAndCondition: yup.boolean().oneOf([true], 'Please Accept terms and Condition')
  })
  const navigate = useNavigate()
  const [email, setEmail] = useState('')

  const search = useLocation().search
  const searchParams = new URLSearchParams(search)
  const {
    control,
    handleSubmit,
    formState: { errors }, setError, setValue
  } = useForm({
    defaultValues: { emailCode: '', acceptTermsAndCondition: true },
    resolver: yupResolver(VerifySchema)
  })

  const resend = async () => {
    const obj = { email }
    await dispatch(generateCode(obj))
  }

  const onSubmit = async data => {
    data.type = type
    if (data.emailCode && data.emailCode.length > 0) {
      setBlock(true)
      await dispatch(verfiyCode(data))
      setBlock(false)
    }
  }
  useEffect(async () => {

    if (store.verifyError !== null) {
      setBlock(false)
      Object.keys(store.verifyError).map((obj) => {
        setError(obj, { type: 'custom', message: store.verifyError[obj][0] })
      })
    }
  }, [store.verifyError])

  useEffect(async () => {
    if (store.accessToken) {
      const data = {}
      data['users'] = store.loginUser
      data['token'] = store.accessToken
      await dispatch(handleLogin(data))

      await dispatch(listOrg({ userId: store.loginUser?.id, type: store.loginUser?.usertype }))
      navigate('/masters/list')

    }
  }, [store.accessToken])

  const sendCode = (val) => {

    if (email) {
      setBlock(true)
      axios.post('/users/sendcode', { email: val ?? email, type })
        .then((res) => {
          setVerifyCodeSent(true)
          setBlock(false)
          toast.success(res.data.users, { position: 'bottom-right' })
        })
        .catch((err) => {
          setBlock(false)
          toast.error(err.response.data.errors, { position: 'bottom-right' })
        })
    }

  }

  useEffect(() => {
    if (!searchParams.get('email')) {
      navigate('/login')
    } else {
      setEmail(searchParams.get('email'))
      sendCode(searchParams.get('email'))
      setValue('email', searchParams.get('email'))
    }
  }, [])

  useEffect(async () => {
    const userdata = JSON.parse(localStorage.getItem('userData'))
    const token = localStorage.getItem('accessToken')
    if (token && userdata) {
      navigate('/masters/list')
    }
  }, [])

  const [timer, setTimer] = useState(0)
  const counter = (val) => {
    setTimer(val)
  }

  const otpfn = (val) => {
    setValue('emailCode', val.join(''))
  }

  return (
    <div className='auth-wrapper auth-basic px-2'>
      <div className='auth-inner my-2'>
        <Card className='mb-0'>
          <CardBody>
            <Link className='brand-logo' to='/' onClick={e => e.preventDefault()}>
              <svg viewBox='0 0 139 95' version='1.1' height='28'>
                <defs>
                  <linearGradient x1='100%' y1='10.5120544%' x2='50%' y2='89.4879456%' id='linearGradient-1'>
                    <stop stopColor='#000000' offset='0%'></stop>
                    <stop stopColor='#FFFFFF' offset='100%'></stop>
                  </linearGradient>
                  <linearGradient x1='64.0437835%' y1='46.3276743%' x2='37.373316%' y2='100%' id='linearGradient-2'>
                    <stop stopColor='#EEEEEE' stopOpacity='0' offset='0%'></stop>
                    <stop stopColor='#FFFFFF' offset='100%'></stop>
                  </linearGradient>
                </defs>
                <g id='Page-1' stroke='none' strokeWidth='1' fill='none' fillRule='evenodd'>
                  <g id='Artboard' transform='translate(-400.000000, -178.000000)'>
                    <g id='Group' transform='translate(400.000000, 178.000000)'>
                      <path
                        d='M-5.68434189e-14,2.84217094e-14 L39.1816085,2.84217094e-14 L69.3453773,32.2519224 L101.428699,2.84217094e-14 L138.784583,2.84217094e-14 L138.784199,29.8015838 C137.958931,37.3510206 135.784352,42.5567762 132.260463,45.4188507 C128.736573,48.2809251 112.33867,64.5239941 83.0667527,94.1480575 L56.2750821,94.1480575 L6.71554594,44.4188507 C2.46876683,39.9813776 0.345377275,35.1089553 0.345377275,29.8015838 C0.345377275,24.4942122 0.230251516,14.560351 -5.68434189e-14,2.84217094e-14 Z'
                        id='Path'
                        className='text-primary'
                        style={{ fill: 'currentColor' }}
                      ></path>
                      <path
                        d='M69.3453773,32.2519224 L101.428699,1.42108547e-14 L138.784583,1.42108547e-14 L138.784199,29.8015838 C137.958931,37.3510206 135.784352,42.5567762 132.260463,45.4188507 C128.736573,48.2809251 112.33867,64.5239941 83.0667527,94.1480575 L56.2750821,94.1480575 L32.8435758,70.5039241 L69.3453773,32.2519224 Z'
                        id='Path'
                        fill='url(#linearGradient-1)'
                        opacity='0.2'
                      ></path>
                      <polygon
                        id='Path-2'
                        fill='#000000'
                        opacity='0.049999997'
                        points='69.3922914 32.4202615 32.8435758 70.5039241 54.0490008 16.1851325'
                      ></polygon>
                      <polygon
                        id='Path-2'
                        fill='#000000'
                        opacity='0.099999994'
                        points='69.3922914 32.4202615 32.8435758 70.5039241 58.3683556 20.7402338'
                      ></polygon>
                      <polygon
                        id='Path-3'
                        fill='url(#linearGradient-2)'
                        opacity='0.099999994'
                        points='101.428699 0 83.0667527 94.1480575 130.378721 47.0740288'
                      ></polygon>
                    </g>
                  </g>
                </g>
              </svg>
              <h2 className='brand-text text-primary ms-1'>Turia</h2>
            </Link>
            <CardTitle tag='h4' className='mb-1'>
              Welcome Turia Books 🚀
            </CardTitle>

            <Form className='auth-register-form mt-2' onSubmit={handleSubmit(onSubmit)}>
              <UILoader blocking={block} loader={<Loader />}>
                <p>Please check mail inbox and spam folder for verification code</p>
                {Object.keys(errors).map((obj, k) => {
                  return <FormFeedback key={k}> {errors[obj]?.message}</FormFeedback>
                })}

                <Row className='gx-2 mb-1'>
                  {
                    !verfiyCodesent &&
                    <Fragment>
                      <Col sm={12} className='mb-1'>
                        <Input
                          type='select'
                          id='rows-per-page'
                          className='w-100 mb-1'
                          value={type}
                          onChange={(e) => setType(e.target.value)}
                          style={{ width: '5rem' }}
                        >
                          <option value='1'>Auditor</option>
                          <option value='2'>Client</option>
                        </Input>
                      </Col>

                      <Col sm={12} className='mb-1'>
                        <Label className='form-label' for='email'>
                          Email
                        </Label>
                        <Controller
                          name='email'
                          control={control}
                          render={({ field }) => (
                            <Input
                              {...field}
                              type='email'
                              id='email'
                              placeholder='jhon@email.com'
                              onChange={(e) => { setEmail(e.target.value); setValue('email', e.target.value) }}
                              className={classnames('form-control', { 'is-invalid': errors.email })}
                            />
                          )}
                        />

                        <Col>
                          {errors.email && <FormFeedback>{errors.email?.message} </FormFeedback>}
                        </Col>
                        <div className='form-check mb-1 mt-1'>
                          <Input type='checkbox' id='terms' />
                          <Label className='form-check-label' for='terms'>
                            Remember me
                          </Label>
                        </div>
                        <Button disabled={!email} className='mt-1' color='primary' block onClick={() => { sendCode() }}>
                          Send OTP
                        </Button>
                      </Col>
                    </Fragment>
                  }
                  {verfiyCodesent &&
                    <Fragment>
                      <span className='font-small-3 text-primary'>{email}</span>
                      <Col sm={12} className='mb-1'>

                        <OTPInput length={4} counter={counter} otpfn={otpfn} />
                        <Row className='d-flex justify-content-between'>
                          <Col>
                            {errors.emailCode && <FormFeedback>{errors.emailCode?.message} </FormFeedback>}
                          </Col>
                          <Col className='d-flex justify-content-end mt-50'>
                            <span aria-disabled={timer > 0} className='text-primary cursor-pointer' onClick={() => { resend() }}>Resend
                              {timer > 0 && <span> ({timer})</span>}
                            </span>
                          </Col>
                        </Row>

                      </Col>
                      {errors['action'] && <p className='text-danger'>{errors['action']?.message}</p>}
                      {errors['emailCode'] && <p className='text-danger'>{errors['emailCode']?.message}</p>}
                      <Col sm={12} className='mb-1 '>
                        <div className='form-check form-check-inline'>
                          <Controller
                            name='acceptTermsAndCondition'
                            control={control}
                            render={({ field }) => (
                              <Input
                                {...field}
                                type='checkbox'
                                defaultChecked={true}
                                name='acceptTermsAndCondition'
                                id='acceptTermsAndCondition'

                              />
                            )}
                          />

                          <Label className={classnames('form-label', { 'text-danger': errors.emailCode })}>
                            <p>By choosing <b>Submit</b> you agree to <b>Turia's <a href="https://www.turia.in/terms">Terms of Use</a>, <a href="https://www.turia.in/privacy">Privacy</a> and <a href="https://www.turia.in/privacy">Data Protection Policies</a>.</b></p>
                          </Label>
                        </div>

                      </Col>
                    </Fragment>
                  }
                </Row>
                {verfiyCodesent &&
                  <div className='d-flex justify-content-between mt-2'>
                    <Button type='submit' color='primary' className='btn-next'>
                      <span className='align-middle d-sm-inline-block d-none'>Submit</span>
                    </Button>
                  </div>
                }
              </UILoader>
            </Form>

          </CardBody>
        </Card>
      </div>
    </div>
  )
}

export default RegisterBasic
